@use 'global' as *;

.aboutus {
  position: relative;
}

.aboutus::before {
  @include mq(md){
    mask-image: url(../images/common/musicalnote1.png);
    mask-position: center center;
    mask-repeat: no-repeat;
    mask-size: cover;
    background-color: $accent-color;
    position: absolute;
    transform: rotate(15deg);
    content: "";
    bottom: rem(-89);
    right: calc(50% - min(48%, rem(637)));
    width: rem(156);
    height: rem(156);
    z-index: 100;
  }
}

.aboutus__inner.inner {
  max-width: rem(600);
  @include mq('md') {
    max-width: initial;
  }
}

.aboutus__wrap {
  margin-top: rem(24);
  @include mq(md){
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: rem(30);
  }
}

.aboutus__wrap--sub {
  margin-top: 0;
}

.aboutus__main-title {
  color: $accent-color;
  font-family: $english-font-family;
  font-size: rem(40);
  @include mq('md') {
    // color: $base-color;
    font-size: rem(46);
    // line-height: calc(100 / 46);
    max-width: rem(400);
    width: 100%;
  }
}

.aboutus__img {
  width: 100%;
  @include mq(md){
    max-width: rem(400);
  }
}

.aboutus__img img {
  width: 100%;
  aspect-ratio: 345 / 228;
  object-fit: cover;
  border-radius: 45px;
  @include mq('md') {
    aspect-ratio: 400 / 606;
  }
}

.aboutus__wrap-right {
  margin-top: rem(16);
  @include mq('md') {
    margin-top: rem(26);
    padding-left: rem(60);
  }
}

.aboutus__text {
  line-height: calc(24 / 16);
  margin-top: rem(24);
  @include mq('md') {
    max-width: rem(400);
    width: 100%;
    margin-top: rem(24);
  }
}

.aboutus__btn {
  margin-top: rem(50);
  text-align: center;
  padding-bottom: rem(3);
  @include mq('md') {
    margin-top: calc(rem(40) + rem(1));
  }
}
